.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);
    background: radial-gradient(
      circle at 30% -100%,
      #042c54 25%,
      rgba(4, 44, 84, 1) 85%,
      rgba(27, 120, 222, 1) 100%
    );
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
  
  .gpt3__whatgpt3-feature {
    display: flex;
  }
  
  .gpt3__whatgpt3-feature .gpt3__features-container__feature {
    margin: 0;
  }
  
  .gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
    max-width: 700px;
  }
  
  .gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 0 2rem;
  }
  
  .gpt3__whatgpt3-heading h1 {
    font-size: 34px;
    font-family: var(--font-family);
    max-width: 510px;
    font-weight: 800;
    line-height: 45px;
  }
  
  .gpt3__whatgpt3-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
  }
  
  .gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row; /* Ensures items flow in a row */
    justify-content: space-between; /* Adjusts space between items */
    align-items: flex-start; /* Aligns items at the start of the container */
    margin-top: 2rem;
  }
  
  @media screen and (max-width:950px)
  {
    .gpt3__whatgpt3-heading
    {
      display:block;
    }

  }